.main {
    min-height: 100vh;
    background-color: var(--background);
    color: var(--foreground);
}

.header {
    height: 70px;
    padding: 0 4%;
    display: flex;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 50;
    background: linear-gradient(to bottom, rgba(0, 0, 0, 0.7) 0%, transparent 100%);
}

.logo {
    color: var(--netflix-red);
    font-size: 2rem;
    font-weight: 800;
    margin: 0;
    letter-spacing: 0.1rem;
}

.content {
    padding-top: 80px;
    min-height: 100vh;
}

.uploadContainer {
    height: 80vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.uploadRow {
    padding: 0 4%;
}